<template>
<div>
  <Nav></Nav>
  <form>
    <div class="form-group">
      <label for="email">Email:</label>
      <input type="email" class="form-control" id="email" placeholder="Enter email">
    </div>
    <div class="form-group">
      <label for="pwd">Password:</label>
      <input type="password" class="form-control" id="pwd" placeholder="Enter password">
    </div>
    <div class="checkbox">
      <label><input type="checkbox"> Remember me</label>
    </div>
    <button type="submit" class="btn btn-default">Submit</button>
    <button @click="get" class="btn btn-default"> </button>
    <button @click="post" class="btn btn-default"> </button>
    <button @click="put" class="btn btn-default"> </button>
    <button @click="del" class="btn btn-default"> </button>
  </form>
  <Footer></Footer>
</div>
</template>

<script>
import Nav from '@/components/member/Nav.vue'
import Footer from'@/components/member/Footer.vue'
import axios from 'axios'
export default {
  components: {
    Nav,
    Footer
  },
  data(){
    return {
      
    }

  },
  methods:{

    get: function(){   // get , post    
        alert('e');
        axios.get('/customers/count')  //  .get.post,put,delete  crud 
        .then(d =>
        {
          console.log(d.data);
          alert(d.data);
        })
        .catch((e)=>{
            alert('')
        }) 
    },

    post: ()=>{
        axios.post('')
        .then(d=>
        {
          alert(`   : ${d.data.result}`)
        })
          .catch((e)=>{
            alert('')
        }) 
    },

    put: ()=>{
      axios.put('/customers/id')
      .then(d=>{
        alert(`put  : ${d.data.result}`)
      })
      .catch((e)=>{
            alert('')
        }) 
    },

    del: ()=>{
      axios.delete('/customers/id')
      .then(d=>{
        alert(`DEL  : ${d.data.result}`)
      })
      .catch((e)=>{
            alert('')
        }) 
    }

  }
}
</script>

<style scoped>
</style>


